import 'vux/src/styles/reset.less';
import './recommended_books.scss'
import Badge from 'vux/src/components/badge/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


import {fetch, rap} from 'js/fetch.js'
let url = {
list: '/merchandiseHot/list.do',
slideList: '/slide/listSlides.do',
getRank:'api/books'
}
url = rap(url)


import Booklist from 'components/booklist/booklist.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
token:'',
showList1: true,
bookList:[],
lists: [
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
}, {
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
{
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
},
],
scrollTop: 0,
onFetching: false,
bottomCount: 20,
n4: 10,
demo4Value: {
pullupStatus: 'default'
}
},
created() {
this.getRank()
},
methods: {
getRank(){
fetch('get', url.getRank, {type:'new_recommend'}).then(res => {
if (res.status >= 200 && res.status <= 300) {
console.log(res.data)










this.$set(this, 'bookList', res.data);


}else {
this.$vux.toast.show({
text: res.data.message,
type: 'warn',
onShow() {

},
onHide() {

}
})
}
})
},
load4 () {
this.demo4Value.pullupStatus = 'up'
setTimeout(() => {
this.lists.push({
title: '婴儿画报2017年第三季度合订本',
img: '/static/book.jpg',
author: '作者金波',
status: '已领取',
})
setTimeout(() => {
this.demo4Value.pullupStatus = 'default'
this.$nextTick(() => {
this.$refs.scroller.reset()
})
}, 10)
}, 2000)
},
show(index){
if (this.listIndex > index) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.listIndex = index
}
},
components: {
Spinner,
Badge,
Group,
Cell,
Panel,
XButton,
Scroller,
Booklist
},
mixins: [mixin]
})



